import React, { useContext } from 'react';
import './CallButton.component.scss';
import variables from '../../styles/_variables.module.scss';
import { AppContext } from '../../AppContext';
import CallButtonIcon from './CallButtonIcon.component.jsx';

export default function CallButton() {
  const { themeColorObj, isLG } = useContext(AppContext);
  const themeColor = themeColorObj[0];

  return (
    <div className="call navlinks">
      <a href="tel:+13053289110">
        <p
          className="navlinks"
          style={
            isLG && themeColor === 'light'
              ? { color: variables.primaryColor, display: 'flex' }
              : isLG && themeColor === 'dark'
              ? { color: variables.backgroundLight }
              : { display: 'none' }
          }
        >
          305-328-9110
        </p>
        <CallButtonIcon
          fillCircle={variables.secondaryColorDark}
          fillPhone={
            themeColor === 'light'
              ? variables.backgroundLight
              : variables.primaryColor
          }
        />
      </a>
    </div>
  );
}
